.projects-list {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	gap:2rem;
	padding-bottom: 1.5rem;
	
}

.project {
	width:calc((100% / 3) - 2rem);

	padding: 2rem;
	background-color: var(--card-bg-color);
	border: 1px solid var(--card-bg-color);
    border-radius: 1rem;
    box-shadow: 0 0.4rem 0.5rem 0 var(--card-shadow-color);

	animation-duration: var(--animate-duration);
  	animation-fill-mode: both;
}

.project:hover {
	animation-name: zoomAnimation;
	animation-timing-function: ease-in-out;
}

.project-logo {
	width: 100%;
    display: flex;
    flex-direction: row;
    gap: 2rem;
    flex-wrap: wrap;
}

.project-logo-box {
	width: 2.5rem;
	height: 2.5rem;
	border-radius: 50%;
	outline: 0.2rem solid white;
	box-shadow: 0px 0.5rem 0.7rem rgba(0, 0, 0, 0.25);
	overflow: hidden;
}

.project-logo .project-logo-img {
	width: 100%;
}

.project-title {
	font-family: var(--title-font);
	color: var(--primary-color);
	padding-top: 3rem;
	margin-bottom: 1em;
	font-size: var(--font-size-large);
	font-weight: 700;
	text-align: left;
}

.project-description {
	text-align: left;
	color: var(--secondary-color);
}
.project-description-item {
	padding-top: 0.5rem;
}

.project-link {
	display: inline-flex;
	align-items: center;
	font-size: var(--font-size-small);
	padding: 0.5rem 2rem;
    color: var(--white-color);
    background-image: linear-gradient(to right, var(--blue-color) 0%, var(--purple-color) 100%);
    margin-top: 1.5rem;
    border-radius: 2rem;
    text-decoration: none;
}

.project-link:hover  {
	box-shadow: 0 0.4rem 0.5rem 0 var(--card-shadow-color);
	transition: box-shadow 0.3s ease-in-out;
}


.project .project-link-text {
	padding-right: 1.2rem;
}

@media (max-width: 1024px) {
	.project {
		width:calc((100% / 2) - 2rem);
	}
}

@media (max-width: 768px) {
	.project {
		width: calc(100% / 1);
	}
}