.resume-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 8rem;
}

.resume-file-container {
	display: flex;
    flex-direction: column;
}
.resume-file-btn {
	display: flex;
    justify-content: center;
}

.resume-file-btn .resume-file-btn-link {
	display: flex;
	align-items: center;
	gap: 0.5rem;
    padding: var(--gap-small) var(--gap-large);
	background-image: linear-gradient(to right, var(--blue-color) 0%, var(--purple-color) 100%);
    margin: var(--gap-middle);
	border-radius: 2rem;

    color: var(--white-color);
    font-size: var(--font-size-small);
    text-decoration: none;
    cursor: pointer;
}

.resume-file-btn .resume-file-btn-link:hover{
	box-shadow: 0 0.4rem 0.5rem 0 var(--card-shadow-color);
	transition: box-shadow 0.3s ease-in-out;
}
.resume-file {
	width: 100%;
}
