.home-container {
	padding-top: 8rem;
}

.home-first-area {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.home-first-area-left-side {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.home-title {
	text-align: left;
	padding: 0rem 2rem;
}

.home-subtitle {
	padding: 0.5rem 1rem;
	text-align: left;
}

.home-first-area-right-side {
	display: flex;
	align-items: center;
}

.home-image-container {
	width: 25rem;
    margin-bottom: 3rem;
}

.home-image-wrapper {
	overflow: hidden;
	border-radius: 2rem;
	transform: rotate(3deg);
	box-shadow: 0px 0.5rem 0.7rem rgba(0, 0, 0, 0.25);

	transform-origin: top center;
	animation-duration: var(--animate-duration);
    animation-fill-mode: both;
	transition: transform 0.2s ease-in-out;
}

.home-image-wrapper:hover {
    animation-name: swing;
}

.home-image-wrapper img {
	width: 100%;
}

.home-socials {
	display: flex;
	justify-content: left;
	align-items: center;
	padding-top: 2rem;
	gap: 1rem;
}

.home-social-link {
	margin: 0;
	padding: 0;
	font-size: var(--font-size-large);
	color: var(--secondary-color);

	animation-duration: var(--animate-duration);
  	animation-fill-mode: both;
}

.home-social-link:hover {
	color:var(--link-color);
	animation-name: pulse;
	animation-timing-function: ease-in-out;
}

.home-after-title {
	display: flex;
}

.home-session-title {
	font-family: var(--title-font);
	font-size: var(--font-size-pro);
	color: var(--secondary-color);
	text-align: left;
	padding: 2rem 0rem 1rem;
}

.home-session {
	padding-top: 4rem;
}

@media (max-width: 1024px) {
	.home-first-area {
		flex-direction: column;
	}

	.home-first-area-left-side {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		width: 100%;
		order: 2;
	}

	.home-title {
		margin-top: 1rem;
		padding: 0rem 1rem;
		order: 2;
	}

	.home-subtitle {
		margin-top: 1rem;
		order: 3;
	}

	.home-first-area-right-side {
		width: 100%;
		order: 1;
		justify-content: center;
	}

	.home-after-title {
		flex-direction: column;
	}
}
