:root {

	--primary-color: #27272a;
	--secondary-color: #333;
	--grey-color: #999;
	--quaternary-color: #e5e7eb;
	--link-color: #3f80f5;
	--white-color: white;
	--card-bg-color:#f3f5f7;
	--card-shadow-color: #dadcde;
	--purple-color: #8f3aed;
	--blue-color: #3f80f5;

	  /* line height */
	  --line-height-small: 1.2;
	  --line-height-middle: 1.4;
	  --line-height-large: 1.6;
	
	  /* font size */
	  --font-size-small: 0.8rem;
	  --font-size: 1rem;
	  --font-size-large: 1.2rem;
	  --font-size-pro: 2rem;
	  --font-size-max: 2.5rem;

	  /* padding/gaps/margins */
	  --gap-0: 0;
	  --gap-small: 0.5rem;
	  --gap-middle: 1rem;
	  --gap-large: 2rem;

	--primary-font: Graphik Web, Helvetica Neue, Helvetica, Arial, Verdana, sans-serif;;
	--title-font: Means Web, Georgia, Times, Times New Roman, serif;
}

*, *::before, *::after { 
	box-sizing: border-box;
	margin: 0; 
}

html,body {
	font-family: var(--primary-font);
	font-size: var(--font-size);
	line-height: var(--line-height-middle);
	text-align: center;
	background-image: linear-gradient(to left, #f6faff 0%, #fefdff 50%, #f6faff 100%);
}

#root {
	height: 100%;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}