.accordion__header {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: left;
}
.accordion__header__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: var(--font-size-large);
	font-family: var(--title-font);
    font-weight: 700;
}

.accordion__header__logo {
	font-family: var(--title-font);
	display: flex;
    padding-bottom: 0.5rem;
    align-items: center;
}

.header__logo-image {
	height: 2rem;
	width: 2rem;
	border-radius: 50%;
	outline: 0.2rem solid white;
	box-shadow: 0px 0.5rem 0.7rem rgba(0, 0, 0, 0.25);
}

.header__title-link {
	font-size: var(--font-size-large);
	font-weight: 700;
	text-align: left;
	padding: 0rem 1rem;
	color: var(--secondary-color);
	text-decoration: none;
}

.accordion__header__subtitle {
    color: var(--secondary-color);
    padding: 1rem 0rem 0rem 3rem;
    font-size: var(--font-size);
    text-align: left;
}

.accordion__header__info {
	display: flex;
	align-items: center;
	justify-content: space-between;
	font-size: var(--font-size-small); 
	padding: 1rem 0rem 0rem 3rem;
	color: var(--grey-color);
}
.accordion__toggle {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;

    width: 100%;
    padding: 2rem 1rem;
    border: 0;
    border-radius: 0;
    border-top: 1px solid var(--grey-color);
    background-color: var(--card-bg-color);
    color: var(--primary-color);

    font-size: var(--font-size-large);
    text-align: left;
    color: var(--secondary-color);
    text-decoration: none;
    cursor: pointer;
}

.accordion__toggle:hover {
    color: var(--link-color);
} 
.accordion__toggle:hover .header__title-link{
    color: var(--link-color);
} 

.accordion__toggle[aria-expanded="true"] {
    border-bottom: 1px solid var(--card-bg-color);
}

.accordion__header:first-of-type .accordion__toggle {
    border-top: 1px solid var(--card-bg-color);
}

.accordion__icon {
    color: inherit;
}

.according__panel {
    padding: 0rem 1rem 2rem 3rem;
    border-radius: 0.3rem;
    margin: 0rem 0rem 2rem;

    font-size: var(--font-size);
    text-align: left;
}

.accordion__detail {
    margin-top: var(--gap-small);
}